import React, { Component } from 'react';
import marked from 'marked';
import hljs from 'highlight.js';
import './index.css';
import 'highlight.js/styles/atelier-dune-light.css';

export default class Markdown extends Component {

    constructor(props){
        super(props);
        marked.setOptions({
            renderer: new marked.Renderer(),
            gfm: true,
            tables: true,
            breaks: true,
            pedantic: false,
            smartypants: false,
            highlight: function (str, lang) {
                // Ê≠§Â§ÑÂà§Êñ≠ÊòØÂê¶ÊúâÊ∑ªÂä†‰ª£Á†ÅËØ≠Ë®Ä
                if (lang && hljs.getLanguage(lang)) {
                    try {
                        // ÂæóÂà∞ÁªèËøáhighlight.js‰πãÂêéÁöÑhtml‰ª£Á†Å
                        const preCode = hljs.highlight(lang, str, true).value;
                        // ‰ª•Êç¢Ë°åËøõË°åÂàÜÂâ≤
                        const lines = preCode.split(/\n/).slice();
                        let html = lines.map((item, index) => {
                            return '<li><span class="line-num" data-line="' + (index + 1) + '">'+(index + 1)+'</span>' + item + '</li>'
                        }).join('')
                        html = '<ol>' + html + '</ol>'
                        return html;
                    } catch (__) {}
                }
                // Êú™Ê∑ªÂä†‰ª£Á†ÅËØ≠Ë®ÄÔºåÊ≠§Â§Ñ‰∏é‰∏äÈù¢ÂêåÁêÜ
                const preCode = hljs.highlightAuto(str).value;
                const lines = preCode.split(/\n/).slice();
                let html = lines.map((item, index) => {
                    return '<li><span class="line-num" data-line="' + (index + 1) + '">'+(index + 1)+'</span>' + item + '</li>'
                }).join('')
                html = '<ol>' + html + '</ol>'
                return html;
            }
        });
    }

    // function(code) {
    //     return hljs.highlightAuto(code).value;
    // },

    shouldComponentUpdate(nextProps) {
        if (nextProps.source !== this.props.source) {
            return true;
        }else{
            return false;
        }
    }

    render(){
        const {source} = this.props;
        return <div
            id="note-content"
            className="article-detail"
            dangerouslySetInnerHTML={{__html: source?marked(source):null}}
        />;
    }
}